html,
body {
  background-color: #222c32;
  font-family: "Open Sans", sans-serif;
}

.profile {
  line-height: 1.2;
  font-size: calc(10px + 1vmin);
}

.profile-pic {
  margin: 3vh;
}

.site-section {
  background-color: #222c32 !important;
  margin: 0 !important;
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.site-section:nth-child(odd) {
  background-color: #a8b5bb !important;
}

.profile-description {
  display: inline-block;
}

@media only screen and (min-width: 1170px) {
  .profile-description {
    width: 50%;
  }
}
